

import styles from './pagination.module.css'
import Container from 'react-bootstrap/Container';
import { httpGetBooks } from '../hooks/requests';
const Pagination = ({postsPerPage, totalPosts, paginate}) => {

   const pageNumbers = []
   for(let i = 1; i <= Math.ceil(totalPosts / postsPerPage); i++ ) {

    pageNumbers.push(i)
   }
   return (
   
    <Container className={styles['container']}>
      <ul className={styles['pagination']}>
         
        <li className={styles['page-item']}>
          <a className={styles['page--item-prev']} href='' >
           Prev
          </a>
        </li>
        {
            pageNumbers.map(number => {

                <li key={number} className={styles['page-item']}>
                    <a onClick={() => (number)} className={styles['page-link']} href='!#' >
                      {number}
                    </a>
                </li>
            })
        }
        
         <li className={styles['page-item']}>
          <a className={styles['page--item-next']} href='' >
           Next
          </a>
        </li>
      </ul>

    </Container>
   )
}

export default Pagination